import __var_0__ from '@stdlib/utils/constant-function';
import __var_1__ from '@stdlib/math/base/assert/is-nan';
import __var_2__ from '@stdlib/math/base/special/gamma';
import __var_3__ from '@stdlib/constants/math/float64-eps';
